.calendar {
  width: sizeCalendar($size-calendar-item);
  display: flex;
  flex-wrap: wrap;
  padding: 10px;
  gap: 1px;

  @each $name, $number in $days {
    &--start-day--#{$name} {
      .calendar__item:first-child {
        margin-left: marginItem($number, $size-calendar-item);
      }
    }
  }

  @for $index from 28 through 31 {
    &--month-length--#{$index} {
      .calendar__item:nth-child(-n + #{$index}) {
        display: inline;
      }
    }
  }

  &__item {
    display: none;
    box-sizing: border-box;
    @include size-item;
    border: 1px solid black;
    background-color: #eee;
  }

  @for $index from 1 through $amount-days {
    &__item:nth-child(#{$index})::before {
      content: "#{$index}";
      @extend %number-item;
    }
  }

  &__item:hover {
    background-color: pink;
    cursor: pointer;
    transform: translateY(-20px);
    transition: all 1s ease-in-out;
  }
}
